import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Button, Col, CustomInput, Form, Row, UncontrolledTooltip } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  Box,
  CALIMA_DANGER,
  CALIMA_INFO,
  CALIMA_ORANGE,
  CALIMA_PRIMARY,
  CALIMA_WARNING,
  ColumnsProps,
  compare,
  DATE_MONTH_YEAR_PATTERN,
  dispatchClose,
  EditViewProps,
  ErroList,
  ERROR,
  format,
  InputGroupInline,
  InputType,
  parseRefToDate,
  showToast,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { Erros } from '../../../../models/dtos/mcg/erros';
import { VerificarErros } from '../../../../models/dtos/mcg/verificarErros';
import { verificarErros } from '../../../../services/mcg.service';
import { multiPageActions, Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ConciliacaoBancariaView from '../conciliacaoBancaria/ConciliacaoBancariaView';
import LancamentoSaldoEditView from '../lancamentoSaldo/LancamentoSaldoEditView';
import LancamentoSaldoSearchView from '../lancamentoSaldo/LancamentoSaldoSearchView';

const maxWidth = 120;

interface VerificarErroMCGProcessView extends EditViewProps<VerificarErros> {}

interface OpenFromFechamentoRestauracao {
  executeSearch: true;
}

const VerificarErroMCGProcessView: FC<VerificarErroMCGProcessView & RouteComponentProps & OpenFromFechamentoRestauracao> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const idEmpresa = globalReducer.globalParameter.empresa.id;
  const formProps = useForm<VerificarErros>({
    defaultValues: {
      dtFinal: format(parseRefToDate(globalReducer.globalParameter.empresaMCG.refFinal, 0), DATE_MONTH_YEAR_PATTERN),
      dtInicial: format(parseRefToDate(globalReducer.globalParameter.empresaMCG.refAtual, 1), DATE_MONTH_YEAR_PATTERN),
      idGerEmpresa: idEmpresa,
    },
    mode: 'onBlur',
  });
  const [isErros, setIsErros] = useState([]);
  const [isShowGridOrAlert, setisShowGridOrAlert] = useState(false);
  const dispatch = useDispatch();

  const errosColumns: ColumnsProps[] = [
    {
      dataField: 'rowNumber',
      hidden: true,
      text: 'Row Number',
    },
    {
      dataField: 'mensagem',
      formatter: (cell, row) => {
        return (
          <div>
            <FontAwesomeIcon icon={row.icon} /> {row.mensagem}
          </div>
        );
      },
      style: (cell, row) => {
        return { color: row.color };
      },
      text: 'Mensagens',
    },
    {
      dataField: 'actions',
      formatter: (cell, row) => {
        const handleClick = () => {
          if (row.className === 'DebitoDiferenteCreditoDTO' || row.className === 'PlanoContaDebitoIgualCreditoDTO') {
            openLancamentoEdit(row.idMcgLancamentoLote);
          } else if (row.className === 'ConciliadoValorDiferenteDTO') {
            openConciliacao(row.dtLancamento, row.planoConta);
          } else {
            openLancamentoSearch(row.dtLancamento, row.planoConta);
          }
        };

        return (
          <Button
            id={'buttonEdit' + row.index}
            color="neutral-secondary"
            onClick={handleClick}
            className="btn-pill d-30 p-0 align-top btn-animated-icon btn-animated-icon--secondary"
          >
            <span className="btn-wrapper--icon">
              <FontAwesomeIcon color={CALIMA_ORANGE} icon={'pen'} />
            </span>
            <UncontrolledTooltip target={'buttonEdit' + row.index} placement="top">
              {row.conciliacaoBancaria ? 'Editar - Consulta Movimentação' : 'Editar - Lançamento/Conciliação Bancária'}:
            </UncontrolledTooltip>
          </Button>
        );
      },
      headerAlign: () => {
        return 'center';
      },
      headerStyle: () => {
        return { width: '7%' };
      },
      style: (cell, row) => {
        return { color: '#f4772e', textAlign: 'center' };
      },
      text: 'Ações',
    },
  ];

  const verifyError = () => {
    const erros = validateExtras();

    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      verificarErros(formProps.getValues(), {
        errorFunction: mensagem => {
          showToast(mensagem, ERROR);
        },
        thenFunction: (result: Erros[]) => {
          setIsErros(result.map((item, index) => ({ ...item, index })));
          setisShowGridOrAlert(true);
        },
      });
    }
  };

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  useEffect(
    () => {
      if (props.executeSearch) {
        verifyError();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const openMultiPage = (
    componente: any,
    isFromVerifyError: boolean,
    idMcgLancamentoLote?: number,
    dtLancamento?: string,
    planoConta?: PlanoConta
  ) => {
    const Page = componente as FC<any>;
    const pageProps = {
      dtLancamento,
      editItemProps: props.editItemProps,
      history: props.history,
      idMcgLancamentoLote,
      isFromVerifyError,
      location: props.location,
      match: props.match,
      planoConta,
      selectedItem: formProps.getValues(),
    };
    dispatch(multiPageActions.showPage(<Page {...pageProps} />));
  };

  const openLancamentoEdit = (idMcgLancamentoLote: number): void => openMultiPage(LancamentoSaldoEditView, true, idMcgLancamentoLote);

  const openConciliacao = (dtLancamento, planoConta): void => openMultiPage(ConciliacaoBancariaView, true, null, dtLancamento, planoConta);

  const openLancamentoSearch = (dtLancamento, planoConta): void => {
    openMultiPage(LancamentoSaldoSearchView, true, null, dtLancamento, planoConta);
  };

  const validateExtras = () => {
    const errors = [];
    if (compare(formProps.getValues().dtFinal, formProps.getValues().dtInicial) === -1) {
      errors.push('Data Final não pode ser menor que a Data Inicial');
    }

    return errors;
  };

  return (
    <WindowComponent
      title="Manutenção de Verificar Erros/Avisos"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiId={173}
    >
      <Form>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <Row>
          <Col xs={3} className="mt-3 ml-3">
            <Row>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="dtInicial"
                isPrependLabel={true}
                labelWidth={maxWidth}
                label="Data Inicial"
                placeholder="Informe a data Inicial."
                type={InputType.DATE}
              />
            </Row>
            <Row>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="dtFinal"
                isPrependLabel={true}
                labelWidth={maxWidth}
                label="Data Final"
                placeholder="Informe a data Final."
                type={InputType.DATE}
              />
            </Row>
            <Row>
              <CustomInput
                type="switch"
                label="Mostrar Avisos de Conciliação"
                name="conciliacaoBancaria"
                id="conciliacaoBancaria"
                innerRef={formProps.register}
              />
            </Row>
          </Col>
          <Col xs={8} className="ml-3">
            <Box title="Legenda">
              <Row>
                <Col>
                  <FontAwesomeIcon color={CALIMA_WARNING} icon="exclamation-triangle" /> Débito Difere do Crédito
                </Col>
                <Col>
                  <FontAwesomeIcon color={CALIMA_INFO} icon="info" /> Conta Crédito e Débito iguais
                </Col>
              </Row>
              <Row>
                <Col>
                  <FontAwesomeIcon color={CALIMA_DANGER} icon="ban" /> Conta Caixa Com Saldo Credor
                </Col>
                <Col>
                  <FontAwesomeIcon color={CALIMA_PRIMARY} icon="exclamation-circle" /> Lançamento Conciliado Com Valor Diferente
                </Col>
              </Row>
            </Box>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <Button className="ml-0" color="primary" onClick={formProps.handleSubmit(verifyError)}>
              <FontAwesomeIcon icon="sync" /> Consultar
            </Button>
          </Col>
          <Col>
            <Button className="float-right" onClick={formProps.handleSubmit(handleCancel)} color="danger">
              <FontAwesomeIcon icon="check-circle" /> Fechar
            </Button>
          </Col>
        </Row>
        {isShowGridOrAlert && (
          <div>
            {isErros.length > 0 ? (
              <>
                <div className="table-area">
                  <BootstrapTable
                    bootstrap4={true}
                    bordered={true}
                    columns={errosColumns}
                    condensed={true}
                    className="table-scroll"
                    data={isErros}
                    hover={true}
                    keyField="index"
                    noDataIndication="Nenhum registro a ser exibido."
                    responsive={true}
                    striped={true}
                    tabIndexCell={true}
                  />
                </div>
                <div key="divFotterKey" className="text-left">
                  Total de Registros: {isErros.length}
                </div>
              </>
            ) : (
              <div className="alert d-flex align-items-center pl-2 align-content-center alert-success alert-dismissible fade show" role="alert">
                <span className="font-size-lg d-block d-40 mr-2 text-center">
                  <FontAwesomeIcon icon="check-circle" />
                </span>
                <span>
                  <strong className="d-block">Não Existem Erros!</strong> Nossas buscas não encontraram nenhum erro em seus lançamentos.{' '}
                  <FontAwesomeIcon icon="smile" />
                </span>
                <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                  <span aria-hidden="true">×</span>
                </button>
              </div>
            )}
          </div>
        )}
      </Form>
    </WindowComponent>
  );
};

export default VerificarErroMCGProcessView;
